import * as React from "react"
import banner1 from '/static/img/banner/Banner_001.png'
import banner2 from '/static/img/banner/Banner_002.jpg'
import "./slide.css"

// Import Swiper React components
import { Navigation, Pagination, Scrollbar, A11y } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
import { Link } from "gatsby"

const bannerimgList = [banner1, banner2]
const bannerTextList = [
  {
    title: "  ",
    content: "     ."
  },
  {
    title: "Dev Dive 2022   ",
    content: "             ."
  },

]

const bannerLinkList = ["#", "https://www.youtube.com/playlist?list=PLOdBZFDkhfV00trSo0SEdHMTaLruDNTzV"]


const swiperSlideList = () => {

  const result: any = [];

  for (let i = 0; i < bannerimgList.length; i++) {
    result.push(


      <Link
        to={bannerLinkList[i]}
      >
        <img
          src={bannerimgList[i]}
          width='100%'
          height='300px'
          className="rounded-3xl"
          style={{ objectFit: "cover", height: '300px' }}
        />
        <div className="
  absolute 
  top-1/2	
  left-1/2	
  banner-text
  text-center	
  text-white
  px-[20px]
  w-full
  md:w-[65%]

  " >
          <p className="inline-block font-bold	text-2xl mr-[7px]">
            {bannerTextList[i].title}
          </p>

          <span className="block text-lg  m-auto mt-2">
            {bannerTextList[i].content}
          </span>
        </div>
      </Link>

    )
  }
  return [...result]



}



const TopBanner = () => {

  return (
    <Swiper
      modules={[Navigation, Pagination, Scrollbar, A11y]}
      spaceBetween={50}
      slidesPerView={1}
      navigation
      pagination={{ clickable: true }}
    >
      {
        swiperSlideList().map((item, index) => <SwiperSlide key={index}>{item}</SwiperSlide>)
      }

      <style>{`
        .swiper-button-next{
          left:50%;
          right:50%;
          transform: translate(475px);
        }

        .swiper-button-prev{
          left:50%;
          right:50%;
          transform: translate(-501px);

        }

        @media(max-width:1150px){
          .swiper-button-next{
            transform: translate(395px);
            
    
          }
          .swiper-button-prev{
            transform: translate(-421px);
          }
        }
    
        @media(max-width:960px){
          .swiper-button-next{
            transform: translate(295px);
    
          }
          .swiper-button-prev{
            transform: translate(-321px);
          }
        }
    
        @media(max-width:768px){
          .swiper-button-next{
            transform: translate(245px);
    
          }
          .swiper-button-prev{
            transform: translate(-271px);
          }
        }
    
        @media(max-width:640px){
          .swiper-button-next{
           display:none;
          }
          .swiper-button-prev{
            display:none;
    
          }
        }

        .swiper-horizontal>.swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction{
          bottom:30px;
        }
        .swiper-pagination-bullet-active{
          background-color:#fff;
          width:6px;
          height: 6px;
        }
        .swiper-pagination-bullet{
          background: rgba(255, 255, 255, 0.8);
          width:6px;
          height: 6px;
        }

        `}
      </style>
    </Swiper>



  )


}



export default TopBanner