import { jsx } from "theme-ui"
import Layout from "./layout"
import Listing from "./list/listing"
// import { navigate } from 'gatsby'
import useMinimalBlogConfig from "../hooks/use-minimal-blog-config"
import useSiteMetadata from "../hooks/use-site-metadata"
// import Title from "./title"
import { Link, withPrefix, withAssetPrefix } from "gatsby"
// import replaceSlashes from "../utils/replaceSlashes"
// import { visuallyHidden } from "../styles/utils"
// @ts-ignore

import Hero from "../texts/hero"
import TopBanner from "./banner/top-banner"
// @ts-ignore
const cateNameList = {
  'engineering': '',
  'data': '',
  'design': '',
  'culture': ''
}

type PostsProps = {
  posts: {
    frontmatter: {
      [key: string]: any
    },
    slug: string
    title: string
    date: string
    excerpt: string
    description: string
    body: string
    timeToRead?: number
    tags?: {
      name: string
      slug: string
    }[],

  }[]
  [key: string]: any,
}
const Homepage = ({ posts }: PostsProps) => {
  if (typeof window !== 'undefined') {

    const location = window.location

    let category = ''
    let cateName = ''
    if (location && "search" in location) {

      if (location.search.includes('category')) {
        category = location.search.split('?')[1].split('category=')[1]
        cateName = cateNameList[category]
      }
    }


    return (

      <Layout selectedCategory={category}>
        <div className="">

          <div className="pt-5 px-auto mx-5">

            <TopBanner />

          </div>
          <div className="w-full  px-5 md:px-10">

            {

              <div className="mt-[80px] md:max-w-md lg:max-w-lg mx-auto">
                <Listing posts={posts} showTags={false} selectedCategory={category != '' ? category : 'engineering'} />
              </div>
              // ) :
              //   (
              //     <Listing posts={posts} showTags={false} selectedCategory='' />

              //   )
            }

          </div>
        </div>

      </Layout>
    )
  } else {
    return null
  }
}


export default Homepage
