import { graphql } from "gatsby"
import React from "react";
import HomepageComponent from "../components/homepage"

const Homepage = ({ data }: any) => {
  const mergedPosts = data.allPost.nodes.map((post: { slug: any; }, index: any) => {
    const { frontmatter } = data.allMdx.nodes.find((fm: { frontmatter: { slug: any; }; }) => post.slug === fm.frontmatter.slug);

    return {
      ...post,
      frontmatter,
    }
  })
  return <HomepageComponent posts={mergedPosts} />
}

export default Homepage

export const query = graphql`
query {
  allPost(sort: { fields: date, order: DESC }, limit: 25) {
    nodes {
      slug
      title
      date(formatString: "YYYY M D")
      description
      tags {
        name
        slug
      }
    }
  }
  allMdx(sort: { fields: frontmatter___date, order: DESC }, limit: 25 ) {
    nodes {
      frontmatter {
        title
        date
        slug
        author
        email
        category
        description

      }
    }
  }
}
`
